import React, {Component} from 'react'
import { css } from '@emotion/core'
import { Table, Card, 
  Button, 
  Tooltip, Modal, 
  Popconfirm, Icon, message,
  Input, Divider,
} from 'antd'
import Highlighter from 'react-highlight-words'
import { Link } from 'Config/routes'

class ListOfEmployee extends Component {

  state = {
    loading: false,
    detailVisible: false,
    editVisible: false
  }

  showModal = keyword => {
    if(keyword === 'detail') {
      this.setState({
        detailVisible: true,
      })
    } else if(keyword === 'edit'){
      this.setState({
        editVisible: true,
      })
    }
  }

  handleOk = keyword => {
    if(keyword === 'detail'){
      this.setState({ loading: true })
      setTimeout(() => {
        this.setState({ loading: false, detailVisible: false })
      }, 3000)
    } else if(keyword === 'edit'){
      this.setState({ loading: true })
      setTimeout(() => {
        this.setState({ loading: false, editVisible: false })
      }, 3000)
    }
  }

  handleCancel = keyword => {
    if(keyword === 'detail') {
      this.setState({ detailVisible: false })
    } else if(keyword === 'edit') {
      this.setState({ editVisible: false })
    }
  }

  getColumnSearchProps = (dataIndex) => ({
    filterDropdown: ({
      setSelectedKeys, selectedKeys, confirm, clearFilters,
    }) => (
      <div style={{ padding: 8 }}>
        <Input
          ref={node => { this.searchInput = node }}
          placeholder={`Search ${dataIndex}`}
          value={selectedKeys[0]}
          onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
          onPressEnter={() => this.handleSearch(selectedKeys, confirm)}
          style={{ width: 188, marginBottom: 8, display: 'block' }}
        />
        {/* <Button
          type="primary"
          onClick={() => this.handleSearch(selectedKeys, confirm)}
          icon="search"
          size="small"
          style={{ width: 90, marginRight: 8 }}
        >
          Search
        </Button> */}
      </div>
    ),
    filterIcon: filtered => <Icon type="search" style={{ color: filtered ? '#1890ff' : undefined }} />,
    onFilter: (value, record) => record[dataIndex].toString().toLowerCase().includes(value.toLowerCase()),
    onFilterDropdownVisibleChange: (visible) => {
      if (visible) {
        setTimeout(() => this.searchInput.select())
      }
    },
    render: (text) => (
      <Highlighter
        highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
        searchWords={[this.state.searchText]}
        autoEscape
        textToHighlight={text.toString()}
      />
    ),
  })

  handleSearch = (selectedKeys, confirm) => {
    confirm()
    this.setState({ searchText: selectedKeys[0] })
  }

  handleReset = (clearFilters) => {
    clearFilters()
    this.setState({ searchText: '' })
  }
  
  render(){
    const text = 'Yakin ingin hapus?'

    function confirm() {
      message.info('Clicked on Yes.')
    }

    const { detailVisible, editVisible, loading } = this.state

    const columns = [{
      title: 'Nama',
      dataIndex: 'nama',
      ...this.getColumnSearchProps('nama'),
    }, {
      title: 'Nik',
      dataIndex: 'nik',
      defaultSortOrder: 'descend',
      sorter: (a, b) => a.nik - b.nik,
      ...this.getColumnSearchProps('nik'),
    }, {
      title: 'Jabatan',
      dataIndex: 'jabatan',
      ...this.getColumnSearchProps('jabatan'),
    }, {
      title: 'Shift',
      dataIndex: 'shift',
      ...this.getColumnSearchProps('shift'),
    }, {
      title: 'Aksi', dataIndex: '', key: 'x', render: () => (
        <div>
          <Tooltip placement="bottom" title="Edit">
            <Button icon="edit" css={css`
            border-color: #ffc30e; 
            color: #ffc30e; 
              &:hover{ 
                border-color: #ffc30e; 
                color: #ffc30e;
              }
              &:focus{ 
                border-color: #ffc30e; 
                color: #ffc30e;
              }
            `} 
            ghost onClick={() => this.showModal('edit')}/>
          </Tooltip>
          <Divider type="vertical" />
          <Tooltip placement="bottom" title="Hapus">
            <Popconfirm placement="top" title={text} onConfirm={confirm} okText="Yes" cancelText="No">
              <Button icon="delete" type="danger" ghost/>
            </Popconfirm>
          </Tooltip>
          <Divider type="vertical" />
          <Tooltip placement="bottom" title="Detail">
            <Button icon="more" type="primary" ghost onClick={() => this.showModal('detail')}/>
          </Tooltip>
        </div>
      )
    }]
    
    const data = [{
      key: '1',
      nama: 'John Brown',
      nik: 11112345,
      jabatan: 'Staf Kasir',
      shift: 'Shift 1'
    }, {
      key: '2',
      nama: 'Jim Green',
      nik: 11112346,
      jabatan: 'Staf Kasir',
      shift: 'Shift 2'
    }, {
      key: '3',
      nama: 'Koko Fork',
      nik: 11112346,
      jabatan: 'Staf Kasir',
      shift: 'Shift 3'
    }, {
      key: '4',
      nama: 'Lala Green',
      nik: 11112346,
      jabatan: 'Staf Kasir',
      shift: 'Shift 4'
    }]

    return(
      <>
      <div><h2>Daftar Karyawan</h2></div>
      <Card css={css`
        border-radius: 8px;
        border-top: 4px solid rgb(24, 144, 255);
        box-shadow: 1px 4px 15px rgba(24, 144, 255, 0.1);
        @media(max-width: 700px) {
          min-width: 77vw;
        }
      `}>
        <div css={css`display: flex; justify-content: flex-end; margin-bottom: 1em;`}>
          <Tooltip placement="bottom" title="Export Excel">
            <Button css={css`
            border: 1px solid #ddd;
            &:hover {
              border-color: green;
            }
              img {
                width: 20px;
              }
            `}>
              <img src="/static/icons/excel.png" />
            </Button>
          </Tooltip>
          <Tooltip placement="bottom" title="Print">
            <Button css={css`
            border: 1px solid #ddd;
            &:hover {
              border-color: green;
            }
              img {
                width: 20px;
              }
            `}>
              <img src="/static/icons/print.png" />
            </Button>
          </Tooltip>
          <Link route="addEmployee">
            <Button type="primary">
              Tambah Karyawan
            </Button>
          </Link>
        </div>
        <div css={css`min-width: 250px; overflow-x: auto;`}>
          <Table columns={columns} dataSource={data} />
        </div>
      </Card>
      <Modal
        visible={detailVisible}
        title="Detail"
        onOk={() => this.handleOk('detail')}
        onCancel={() => this.handleCancel('detail')}
        footer={false}
        width="700px"
      >
        <div css={css`
          display: grid;
          grid-template-columns: 1fr 2fr;
          grid-column-gap: 1em;
          justify-items: center;
        `}>
          <div css={css`
            height: 150px;
            width: 150px;
            border-radius: 50%;
            position: relative;
            background-size: cover;
            background-repeat: no-repeat;
            background-image: url('/static/defaultplaceholder.png');
          `} 
          className="photoEmployee"
          />
          <div css={css`
              display: grid;
              grid-row-gap: 2em;
            .detailContent{
              display: grid;
              grid-template-columns: 1fr 2fr;
              grid-column-gap: 1em;
            }
          `}>
            <div className="detailContent">
              <label>Nama Lengkap</label>
              <label><span>:</span> Alifia Indra</label>
            </div>
            <div className="detailContent">
              <label>NIK</label>
              <label><span>:</span> 1223344</label>
            </div>
            <div className="detailContent">
              <label>Email</label>
              <label><span>:</span> example.mail.com</label>
            </div>
            <div className="detailContent">
              <label>Jenis Kelamin</label>
              <label><span>:</span> Pria</label>
            </div>
            <div className="detailContent">
              <label>Tempat/Tanggal Lahir</label>
              <label><span>:</span> Jakarta, 22 Maret 1993</label>
            </div>
            <div className="detailContent">
              <label>No. Telepon</label>
              <label><span>:</span> 087676556458</label>
            </div>
            <div className="detailContent">
              <label>Jabatan</label>
              <label><span>:</span> Staf Kasir</label>
            </div>
            <div className="detailContent">
              <label>Shift</label>
              <label><span>:</span> Shift I | 06.00 - 12.00</label>
            </div>
          </div>
        </div>
      </Modal>
      </>
    )
  }
}

export default ListOfEmployee